import { Link, useLocation, useNavigate } from "react-router-dom";
import styled from "styled-components";
import { AnimatePresence, motion, useAnimation, useViewportScroll } from "framer-motion";
import { useState } from "react";
import { useEffect } from "react";
import { useForm } from "react-hook-form";
import { BsCaretDownFill } from "react-icons/bs";
import { useRecoilState } from "recoil";
import { searchKeyword } from '../atoms'

const Nav = styled(motion.nav)`
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  width: 100%;
  top: 0;
  font-size: 14px;
  padding: 20px 160px 20px 60px;
  color: white;
  box-sizing: border-box;
  z-index: 1;
`;

const Col = styled.div`
  display: flex;
  align-items: center;
`;

const Logo = styled(motion.svg)`
  margin-right: 50px;
  width: 95px;
  height: 25px;
  fill: ${(props) => props.theme.red};
  path {
    stroke-width: 6px;
    stroke: white;
  }
`;

const Items = styled.ul`
  display: flex;
  align-items: center;
`;

const Item = styled.li`
  margin-right: 20px;
  color: ${(props) => props.theme.white.darker};
  transition: color 0.3s ease-in-out;
  position: relative;
  display: flex;
  justify-content: center;
  flex-direction: column;
  &:hover {
    color: ${(props) => props.theme.white.lighter};
  }
  a {
    color: white;
    text-decoration: none;
    display: flex;
    justify-content: center;
    flex-direction: column;
  }
`;

const Search = styled(motion.form)`
  position: relative;
  display: flex;
  color: white;
  align-items: center;
  cursor: pointer;

  svg {
    height: 25px;
    fill: white;
  }
`;

const Input = styled(motion.input)`
  position: absolute;
  right:0;
  transform-origin: right center;
  background-color: transparent;
  border: 0;
  color: #ddd;
  border: 2px solid #ddd;
  padding: 8px 15px;
  padding-left: 45px;
  z-index: -1;
`;

const Circle = styled(motion.span)`
  position: absolute;
  width: 5px;
  height: 5px;
  border-radius: 5px;
  bottom: -5px;
  left: 0;
  right: 0;
  margin: 0 auto;
  background-color: ${(props) => props.theme.red};
`;

const Profile = styled.div`
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  align-items: flex-start;
  color: white;
  cursor: pointer;
  margin-left: 30px;
  height: 150px;
  padding: 16px 0;
  width: 125px;

  svg {
    margin-left: 10px;
    margin-top: 13px;
  }
`;

const ProfileBtn = styled.img`
  width: 36px;
  height: 36px;
  border-radius: 8px;
`;

const ProfileList = styled.ul`
  position: absolute;
  display: flex;
  flex-direction: column;
  top: 60px;
  right: 50px;
  background-color: ${props => props.theme.black.veryDark};
  z-index: 10;

  li {
    display: flex;
    align-items: center;
    width: 100%;
    padding: 10px 20px;

    &:hover {
      text-decoration: underline;
    }

    img {
      width: 36px;
      height: 36px;
      border-radius: 8px;
      margin-right: 10px;
    }
  }

  div {
    padding: 10px 0;
    border-top: 1px solid ${props => props.theme.black.lighter};

    p {
      padding: 5px 20px;
      &:hover {
        text-decoration: underline;
      }
    }
  }
`;

const logoVariants = {
  normal: {
    fillOpacity: 1,
  },
  active: {
    fillOpacity: [0, 1, 0],
    transition: {
      repeat: Infinity,
    },
  },
};

const navVariants = {
  top: {
    backgroundColor: 'rgba(0,0,0,0)',
  },
  scroll: {
    backgroundColor: 'rgba(0,0,0,1)',
  },
}

interface IForm {
  keyword: string;
}

function Header() {
  const [searchOpen, setSearchOpen] = useState(false);
  const [profileOpen, setProfileOpen] = useState(false);
  const matchLocation = useLocation();
  const inputAnimation = useAnimation();
  const navAnimation = useAnimation();
  const { scrollY } = useViewportScroll();
  const toggleSearch = () => {
    if(searchOpen) {
      // trigger the close animation
      inputAnimation.start({
        scaleX: 0,
      });
    } else {
      // trigger the open animation
      inputAnimation.start({
        scaleX: 1,
      });
    }
    setSearchOpen(prev => !prev)
  };
  useEffect(()=>{
    scrollY.onChange(()=> {
      if(scrollY.get() > 80) {
        navAnimation.start("scroll");
      } else {
        navAnimation.start("top");
      }
    });
  }, [scrollY, navAnimation]);
  const navigate = useNavigate();
  const {register, handleSubmit, reset } = useForm<IForm>();
  const [keyword, setKeyword] = useRecoilState<string>(searchKeyword);

  const onValid = (data: IForm) => {
    setKeyword(data?.keyword);
    navigate(`/search?keyword=${data.keyword}`);
  };
  
  return (
    <Nav 
      variants={navVariants}
      animate={navAnimation}
      initial={"top"} 
    >
      <Col>
        <Logo
          variants={logoVariants}
          whileHover="active"
          initial="normal"
          xmlns="http://www.w3.org/2000/svg"
          width="1024"
          height="276.742"
          viewBox="0 0 1024 276.742"
        >
          <motion.path d="M140.803 258.904c-15.404 2.705-31.079 3.516-47.294 5.676l-49.458-144.856v151.073c-15.404 1.621-29.457 3.783-44.051 5.945v-276.742h41.08l56.212 157.021v-157.021h43.511v258.904zm85.131-157.558c16.757 0 42.431-.811 57.835-.811v43.24c-19.189 0-41.619 0-57.835.811v64.322c25.405-1.621 50.809-3.785 76.482-4.596v41.617l-119.724 9.461v-255.39h119.724v43.241h-76.482v58.105zm237.284-58.104h-44.862v198.908c-14.594 0-29.188 0-43.239.539v-199.447h-44.862v-43.242h132.965l-.002 43.242zm70.266 55.132h59.187v43.24h-59.187v98.104h-42.433v-239.718h120.808v43.241h-78.375v55.133zm148.641 103.507c24.594.539 49.456 2.434 73.51 3.783v42.701c-38.646-2.434-77.293-4.863-116.75-5.676v-242.689h43.24v201.881zm109.994 49.457c13.783.812 28.377 1.623 42.43 3.242v-254.58h-42.43v251.338zm231.881-251.338l-54.863 131.615 54.863 145.127c-16.217-2.162-32.432-5.135-48.648-7.838l-31.078-79.994-31.617 73.51c-15.678-2.705-30.812-3.516-46.484-5.678l55.672-126.75-50.269-129.992h46.482l28.377 72.699 30.27-72.699h47.295z" />
        </Logo>
        <Items>
          <Item>
            <Link to="">홈 {matchLocation?.pathname === '/' && <Circle layoutId="circle" />}</Link>
          </Item>
          <Item>
            <Link to="tv">TV 시리즈 {matchLocation?.pathname === '/tv' && <Circle layoutId="circle" />}</Link>
          </Item>
        </Items>
      </Col>
      <Col>
        <Search onSubmit={handleSubmit(onValid)}>
          <motion.svg
            onClick={toggleSearch}
            animate={{x: searchOpen ? -195 : 0}}
            transition={{type: 'linear'}}
            fill="currentColor"
            viewBox="0 0 20 20"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              fillRule="evenodd"
              d="M8 4a4 4 0 100 8 4 4 0 000-8zM2 8a6 6 0 1110.89 3.476l4.817 4.817a1 1 0 01-1.414 1.414l-4.816-4.816A6 6 0 012 8z"
              clipRule="evenodd"
            ></path>
          </motion.svg>
          <Input
            {...register('keyword', { required:true, minLength: 2 })}
            animate={inputAnimation}
            initial={{ scaleX: 0 }}
            transition={{ type: 'linear' }} 
            placeholder="Search for movie or tv show..."
          />
        </Search>
        <Profile 
          onClick={()=> setProfileOpen(prev => !prev)}
          onMouseOver={()=> setProfileOpen(true)}
        >
          <ProfileBtn src={`${process.env.PUBLIC_URL}/img/profile.png`} />
          <BsCaretDownFill style={{ 
            transition: '0.3s', 
            transform: `rotateZ( ${profileOpen ? '180deg' : '0' })`
          }} />
          { profileOpen ? (
            <ProfileList 
            onMouseOut={()=> setProfileOpen(false)}>
              <li>
                <ProfileBtn src={`${process.env.PUBLIC_URL}/img/profile.png`} />
                Anna
              </li>
              <li>
                <ProfileBtn src={`${process.env.PUBLIC_URL}/img/profile.png`} />
                Olaf
              </li>
              <li>
                <ProfileBtn src={`${process.env.PUBLIC_URL}/img/user3.png`} />
                Christopher
              </li>
              <div>
                <p>계정</p>
                <p>고객센터</p>
                <p>넷플릭스에서 로그아웃</p>
              </div>
            </ProfileList> ) : null }
        </Profile>
      </Col>
    </Nav>
  );
}

export default Header;